
import { ref, defineComponent, onMounted } from 'vue';
import ProductCard from './ProductCard.vue';
import NewProductModal from './NewProductModal.vue';
import ProductModel from './ProductModel';
import ProductService from './ProductService';

export default defineComponent({
  components: {
    ProductCard,
    NewProductModal,
  },
  setup() {
    const productModal = ref(false);
    const productRef = ref();
    const isEditing = ref(false);
    const products = ref<ProductModel[]>([]);

    const getProducts = async () => {
      try {
        products.value = await ProductService.getProducts();
      } catch (error) {
        console.log(error);
      }
    };

    const newProductAction = () => {
      isEditing.value = false;
      productRef.value.resetForm();
      productModal.value = true;
    };

    const editProduct = (product: ProductModel) => {
      isEditing.value = true;
      productRef.value.setToEdit(product);
      productModal.value = true;
    };

    const deleteProduct = (id: string) => {
      console.log(id);
    };

    const refreshList = async () => {
      console.log('reset-list');
      getProducts();
      productModal.value = !productModal.value;
    };

    onMounted(async () => {
      try {
        getProducts();
      } catch (error) {
        console.log(error);
      }
    });

    return {
      products,
      isEditing,
      productModal,
      productRef,
      refreshList,
      editProduct,
      deleteProduct,
      newProductAction,
    };
  },
});
